.services {
  background: #1c1c1c;
  padding-top: 183px;
  padding-bottom: 249px;
  &__title {
    letter-spacing: 1.2px;
    padding-right: 33px;
  }
}

.services__inner-main {
  width: 663px;
  height: 663px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(
    50% 50% at 50% 50%,
    #17858c 0%,
    rgba(4, 59, 63, 0) 90%
  );
  &::after {
    border-radius: 50%;
    inset: 90px;
    padding: 1.7px;
  }

  img {
    z-index: 1;
  }
}

.services__subtitle {
  margin-top: 10px;
  padding-right: 33px;
  line-height: 31px;
  margin-bottom: 26px;
  text-align: center;
}

.services__main {
  display: flex;
  justify-content: space-between;
  min-height: 663px;
  font-family: $secondary-font;

  &-item {
    max-width: 350px;
    flex: 1;
    height: 100%;
    margin-bottom: 50px;
    margin-top: 17px;
  }

  &-number {
    font-family: $primary-font;
    color: $gold;
    font-size: 40px;
    margin-bottom: 5px;
    letter-spacing: 1.2px;
    position: relative;
  }

  &-title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &-text {
    color: #d9d9d9;
    font-size: 16px;
  }

  &-button {
    position: relative;
    height: 100%;
    .button {
      position: absolute;
      bottom: 0;
      right: 0;
      margin-bottom: 8px;
    }
  }

  &-col {
    display: flex;
    flex-direction: column;
    &:last-child {
      text-align: right;
    }
  }

  .services__main-col--left .services__main-number {
    &::before {
      content: "";
      border-radius: 50%;
      background: #17868d;
      position: relative;
      display: inline-block;
      top: -10px;
      width: 10px;
      height: 10px;
      margin-right: 11px;
      filter: drop-shadow(0px 0px 6px rgba(32, 204, 215, 0.8));
    }

    &::after {
      position: absolute;
      top: 17px;
      width: 384px;
      height: 55px;
      right: -129px;
    }
  }

  .services__main-col--right .services__main-number {
    &::after {
      content: "";
      border-radius: 50%;
      background: #17868d;
      position: relative;
      display: inline-block;
      top: -10px;
      width: 10px;
      height: 10px;
      margin-left: 11px;
      right: 0;
      filter: drop-shadow(0px 0px 6px rgba(32, 204, 215, 0.8));
      transform: none;
    }

    &::before {
      border-radius: none;
      background: none;
      position: absolute;
      display: inline-block;
      top: 17px;
      width: 384px;
      height: 55px;
      margin-right: 0;
      left: -129px;
      filter: none;
      transform: none;
    }
  }

  .services__inner-main {
    align-self: center;
  }
}

.services__main-col--left .services__main-item:nth-child(1) {
  .services__main-number {
    &::after {
      content: url(../img/svg/line4.svg);
      transform: scale(-1, 1);
    }
  }
}

.services__main-col--left .services__main-item:nth-child(2) {
  .services__main-number {
    &::before {
      margin-right: 16px;
    }
    &::after {
      content: url(../img/svg/line5.svg);
      transform: scale(-1, 1);
    }
  }
}

.services__main-col--left .services__main-item:nth-child(3) {
  margin-top: 45px;

  .services__main-number {
    &::before {
      margin-right: 19px;
    }
    &::after {
      content: url(../img/svg/line3.svg);
    }
  }
}

.services__main-col--right .services__main-item:nth-child(1) {
  .services__main-number {
    &::before {
      content: url(../img/svg/line4.svg);
      transform: none;
    }
    &::after {
      content: '';
    }
  }
}

.services__main-col--right .services__main-item:nth-child(2) {
  margin-top: 51px;
  .services__main-number {
    &::before {
      content: url(../img/svg/line5.svg);
    }
    &::after {
      content: '';
    }
  }
}

@media (max-width: $breakpointTablet) {
  .services__main {
    flex-wrap: wrap;

    &-col:last-child {
      text-align: left;
    }

    &-col-image {
      order: 1;
      flex: 1;
    }
  }
}

@media (max-width: $breakpointMobile) {
  .services__main {
    &-col-image {
      display: none;
    }

    &-button {
      .button {
        position: relative;
      }
      margin-left: 0;
    }
  }
}

@media (max-width: $breakpoint1500) {
  .services__main-col--right .services__main-number {
    &::before {
      display: none !important;
    }
  }
  .services__main-col--left .services__main-number {
    &::after {
      display: none;
    }
  }
}
